/* variaveis */

:root{
    --main-red: rgba(211,9,19);
    --main-red-houver: #e50914;
    --main-dark: rgba(20, 20, 20);
    --main-deep-dark: rgba(0, 0, 0);
    --main-grey: rgba(153, 153, 153);
}

*{
    box-sizing: border-box;
}

body{
    font-family: 'Helvetica';
    -webkit-font-smoothing:  antialiased;
    background: var(--main-dark);
    overflow-x: hidden;

    color: #fff
}

a{
    text-decoration: none;    
    color: white;
}

/* Header Section */

.header-container {
    width: 100vw;
    height: 100vh;
    position: relative;
    background: radial-gradient(rgb(0,0,0,0), rgba(0,0,0,0.7)),
    url(../images/bg.jpg) no-repeat center center/cover;
}

.header-container::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0, 0.5);
    box-shadow: inset 4.375rem 3.125rem 11.875rem #000,
        inset -4.375rem 3.125rem -11.875rem;
}

.header-content{
    width: 65%;
    position: relative;
    margin: 4.5rem auto 0;
    display: flex;
    justify-content: center;
    align-content: center;
    text-align: center;
    flex-direction: column;
    z-index: 1;
}

